.filter {
    h4 {
        margin-top: 1rem;
    }

    .continent button {
        display: block;
        text-align: left;
        width: 80%;
        height: 3rem;
        font-size: 1.5rem;
        border: none;
        background-color: transparent;
        cursor: pointer;
        border-bottom: 1px solid #777;
    }

    .country {
        select {
            font-size: 1.6rem;
            font-weight: 300;
            padding: 5px;

            width: 80%;
            border: 1px solid #777;
            border-radius: 3px;
            outline: none;
        }
    }
}

@media screen and (max-width: 700px) {
    .filter {
        .continent button {
            width: 100%;
        }

        .country {
            select {
                width: 100%;
            }
        }
    }
}

.active {
    position: relative;
    padding-left: 1rem;
}

.active::before {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 2px;
    height: 100%;
    background-color: var(--color-danger);
}